            <span>ÈÄâÊã©ÁªüËÆ°</span>
          </div>

          <div class="choice-stats">
            <div v-for="choice in choices" :key="choice.value" class="choice-stat-item">
              <div class="choice-stat-header">
                <span class="choice-stat-icon">{{ choice.icon }}</span>
                <span class="choice-stat-name">{{ choice.name }}</span>
              </div>
              <div class="choice-stat-bar">
                <el-progress
                  :percentage="getChoicePercentage(choice.value)"
                  :show-text="false"
                  :stroke-width="8"
                ></el-progress>
                <span class="choice-stat-count">{{ choiceStats[choice.value] || 0 }}Ê¨°</span>
              </div>
            </div>
          </div>
        </el-card>

        <el-card class="history-card" style="margin-top: 20px;">
