<script>
export default {
  name: 'RockPaperScissorsView',
  metaInfo: {
    title: 'ÁåúÊã≥Ê®°ÊãüÂô® - Áü≥Â§¥Ââ™ÂàÄÂ∏ÉÊ∏∏Êàè',
    meta: [
      { name: 'description', content: 'ÁªèÂÖ∏ÁöÑÁü≥Â§¥Ââ™ÂàÄÂ∏ÉÁåúÊã≥Ê∏∏ÊàèÔºåÊîØÊåÅÊâãÂä®ÂíåËá™Âä®Ê®°ÂºèÔºåÂÆûÊó∂ÁªüËÆ°ËÉúÁéáÂíåÊ∏∏ÊàèÂéÜÂè≤„ÄÇ' },
      { name: 'keywords', content: 'ÁåúÊã≥Ê∏∏Êàè,Áü≥Â§¥Ââ™ÂàÄÂ∏É,ÁåúÊã≥Ê®°ÊãüÂô®,Âú®Á∫øÊ∏∏Êàè,‰ºëÈó≤Ê∏∏Êàè' }
    ]
  },
  data() {
    return {
      choices: [
        { value: 'rock', name: 'Áü≥Â§¥', icon: 'üóø' },
        { value: 'paper', name: 'Â∏É', icon: 'üìÑ' },
        { value: 'scissors', name: 'Ââ™ÂàÄ', icon: '‚úÇÔ∏è' }
      ],
      playerChoice: null,
      computerChoice: null,
      gameResult: null,
      isPlaying: false,

      // ÁªüËÆ°Êï∞ÊçÆ
      stats: {
        wins: 0,
        draws: 0,
        losses: 0
      },
      choiceStats: {
        rock: 0,
        paper: 0,
        scissors: 0
      },
      gameHistory: [],

      // Ëá™Âä®Ê∏∏Êàè
      isAutoPlaying: false,
      autoGameCount: 100,
      autoCurrentCount: 0,
      autoTimer: null
    }
  },
  computed: {
    totalGames() {
      return this.stats.wins + this.stats.draws + this.stats.losses
    },
    winRate() {
      if (this.totalGames === 0) return 0
      return Math.round((this.stats.wins / this.totalGames) * 100)
    },
    autoProgress() {
      if (this.autoGameCount === 0) return 0
      return Math.round((this.autoCurrentCount / this.autoGameCount) * 100)
    },
    recentHistory() {
      return this.gameHistory.slice(-10).reverse()
    }
  },
  methods: {
    makeChoice(choice) {
      if (this.isPlaying) return

      this.isPlaying = true
      this.playerChoice = choice
      this.computerChoice = this.getRandomChoice()

      // Êõ¥Êñ∞ÈÄâÊã©ÁªüËÆ°
      this.choiceStats[choice]++

      // ËÆ°ÁÆóÁªìÊûú
      const result = this.calculateResult(choice, this.computerChoice)

      // Êõ¥Êñ∞ÁªüËÆ°
      this.updateStats(result)

      // Ê∑ªÂä†Âà∞ÂéÜÂè≤ËÆ∞ÂΩï
      this.addToHistory(choice, this.computerChoice, result)

      // ÊòæÁ§∫ÁªìÊûú
      this.showResult(result)

      // ÈáçÁΩÆÁä∂ÊÄÅ
      setTimeout(() => {
        this.isPlaying = false
      }, 2000)
    },

    getRandomChoice() {
      const randomIndex = Math.floor(Math.random() * this.choices.length)
      return this.choices[randomIndex].value
    },

    calculateResult(player, computer) {
      if (player === computer) {
        return 'draw'
      }

      const winConditions = {
        rock: 'scissors',
        paper: 'rock',
        scissors: 'paper'
      }

      return winConditions[player] === computer ? 'win' : 'lose'
    },

    updateStats(result) {
      switch (result) {
        case 'win':
          this.stats.wins++
          break
        case 'draw':
          this.stats.draws++
          break
        case 'lose':
          this.stats.losses++
          break
      }
    },

    addToHistory(player, computer, result) {
      const resultTexts = {
        win: 'ËÉúÂà©',
        draw: 'Âπ≥Â±Ä',
        lose: 'Â§±Ë¥•'
      }

      this.gameHistory.push({
        player,
        computer,
        result,
        resultText: resultTexts[result],
        timestamp: Date.now()
      })

      // Âè™‰øùÁïôÊúÄËøë1000Êù°ËÆ∞ÂΩï
      if (this.gameHistory.length > 1000) {
        this.gameHistory = this.gameHistory.slice(-1000)
      }
    },

    showResult(result) {
      const messages = {
        win: 'üéâ ‰Ω†Ëµ¢‰∫ÜÔºÅ',
        draw: 'ü§ù Âπ≥Â±ÄÔºÅ',
        lose: 'üò¢ ‰Ω†Ëæì‰∫ÜÔºÅ'
      }

      this.gameResult = {
        type: result,
        message: messages[result]
      }
    },

    getChoiceIcon(choice) {
      const choice_obj = this.choices.find(c => c.value === choice)
      return choice_obj ? choice_obj.icon : '‚ùì'
    },

    getChoiceName(choice) {
      const choice_obj = this.choices.find(c => c.value === choice)
      return choice_obj ? choice_obj.name : 'Êú™Áü•'
    },

    getChoicePercentage(choice) {
      if (this.totalGames === 0) return 0
      const total = Object.values(this.choiceStats).reduce((sum, count) => sum + count, 0)
      if (total === 0) return 0
      return Math.round((this.choiceStats[choice] / total) * 100)
    },

    getWinRateColor(rate) {
      if (rate >= 60) return '#67c23a'
      if (rate >= 40) return '#e6a23c'
      return '#f56c6c'
    },

    formatProgress() {
      return `${this.autoCurrentCount}/${this.autoGameCount}`
    },

    startAutoGame() {
      if (this.isAutoPlaying) return

      this.isAutoPlaying = true
      this.autoCurrentCount = 0

      this.autoTimer = setInterval(() => {
        if (this.autoCurrentCount >= this.autoGameCount) {
          this.stopAutoGame()
          return
        }

        const playerChoice = this.getRandomChoice()
        const computerChoice = this.getRandomChoice()

        // Êõ¥Êñ∞ÈÄâÊã©ÁªüËÆ°
        this.choiceStats[playerChoice]++

        // ËÆ°ÁÆóÁªìÊûú
        const result = this.calculateResult(playerChoice, computerChoice)

        // Êõ¥Êñ∞ÁªüËÆ°
        this.updateStats(result)

        // Ê∑ªÂä†Âà∞ÂéÜÂè≤ËÆ∞ÂΩï
        this.addToHistory(playerChoice, computerChoice, result)

        this.autoCurrentCount++
      }, 50) // ÊØè50ms‰∏ÄÂ±ÄÊ∏∏Êàè
    },

    stopAutoGame() {
      this.isAutoPlaying = false
      if (this.autoTimer) {
        clearInterval(this.autoTimer)
        this.autoTimer = null
      }
    },

    resetGame() {
      this.stats = {
        wins: 0,
        draws: 0,
        losses: 0
      }
      this.choiceStats = {
        rock: 0,
        paper: 0,
        scissors: 0
      }
      this.gameHistory = []
      this.gameResult = null
      this.playerChoice = null
      this.computerChoice = null
      this.stopAutoGame()
    },

    clearHistory() {
      this.gameHistory = []
    }
  },

  beforeDestroy() {
    this.stopAutoGame()
  }
}
</script>

